import React from "react";
import Styled from "styled-components";

const Badges = function(props) {
  const Badge = Styled.div`
        font-size: 6em;
        align-items: center;
        margin: 5px;
        color: indigo;

        @media (max-width: 700px) {
            font-size: 2em;
            width:1.25em;
            height:1.25em;
        }
    `;

  return <Badge>{props.badge}</Badge>;
};

export default Badges;



