import React, { Component } from "react";
import Styled from "styled-components";

import img from "../assets/chattish.png";
import Badges from "./Badges";

class Projects extends Component {
    constructor() {
        super();
        this.state = {
            react: <i className="fab fa-react" />,
            socketio: <i className="icon-socketio"/>,
            aws: <i className="fab fa-aws" />,
            rethinkdb : <i className="icon-rethinkdb" />
        }
    }
  render() {
    const Container = Styled.div`
            height: 450px;
            display: flex;
            justify-content: space-around;
            align-items: center;
            border-bottom: 5px solid Tomato;

            @media (max-width: 700px) {
                flex-direction: column;
                justify-content: space-between;
			}
        `;

    const Div = Styled.div`
        display: flex;
        justify-content: space-around;
        align-items: center;
        border-bottom: 5px solid Tomato;

        @media (max-width: 700px) {
            flex-direction: column;
        }
    `;

    const P = Styled.div`  
        width: 450px;
        font-size: 1.010em;
        letter-spacing: 2px;

        @media (max-width: 700px) {
            font-size: .75em;
        }
        `;

    const Img = Styled.div`
        background-image: url(${img});
        background-size: 350px;
        -webkit-box-shadow:0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
        -moz-box-shadow:0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
        box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
        width: 350px;
        height: 350px;

       
        @media (max-width: 900px) {
            background-size: 250px;
            width: 250px;
            height: 250px;
        }
       
       
       
        @media (max-width: 700px) {
            background-size: 150px;
            width: 150px;
            height: 150px;
        }
        `;

        const Button = Styled.div`
            width: 100px;
            height: 50px;
            background: #302b63;
        `;

        const Techs = Styled.div`
        
        `;

    return (
      <Container>
        <Img />
        <Techs>
            <Badges badge={this.state.react} />
            <Badges badge={this.state.socketio} />
            <Badges badge={this.state.rethinkdb} />
            <Badges badge={this.state.aws} />
        </Techs>
        <P>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent
          aliquet justo mauris, eget posuere lectus ultricies sed. Donec tempus
          imperdiet arcu, vitae dapibus nulla pellentesque id. Vivamus vitae
          ligula et nisl accumsan suscipit. Nulla sodales semper arcu, ac
          laoreet sapien congue sit amet. Nulla metus nunc, gravida tristique
          mauris vitae, gravida placerat turpis. Nullam ac vestibulum justo. In
          in eros sem. Ut accumsan lobortis commodo. Class aptent taciti
          sociosqu ad litora torquent per conubia nostra, per inceptos
          himenaeos. Nulla vestibulum maximus sem eget ullamcorper. Aliquam
          dignissim sem ac tristique rutrum. Maecenas a tellus iaculis, feugiat
          sapien nec, rutrum ante.
          <br /> <br />
          <Button>Link</Button>
        </P>
      </Container>
    );
  }
}

export default Projects;



